<template>

  <main class="content">
    <div class="container-fluid p-0">
       <div hidden id="spinner"></div>
      <div class="page-title-right">
        <ol class="breadcrumb m-0">
          <li class="breadcrumb-item">
            <a href="javascript: void(0);">Anasayfa</a>
          </li>
          <li class="breadcrumb-item active">Bom Güncelle</li>
        </ol>
      </div>
      <br />
      <h1 class="h3 mb-3">Bom Güncelle</h1>
      <div class="row">
        <div class="col-12">
          <div class="card">
            <div class="card-body">
              <div class="row">
                <div class="col-xl-6">
                  <label for>Proje kodunu girip Arama butonuno basınız</label>
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      class="form-control"
                      placeholder="Proje kodunu giriniz"
                      v-model="project"
                    />
                    <div class="input-group-append">
                      <button class="btn btn-outline-secondary" type="button" @click="getbom">Ara</button>
                    </div>
                  </div>
                </div>
                <div class="col-md-12">
                  <template v-if="iftable">
                  <vue-good-table
                    :columns="columns"
                    :rows="rows"
                   :sort-options="{
                      enabled: true,
                       initialSortBy: [
                        {field: 'Tanim', type: 'asc'},
                        {field: 'Stokcode', type: 'desc'}
                      ],
                       }"
                    :pagination-options="{
                       enabled: true,
                      mode: 'pages'
                       }"
                  >
                    <template   slot="table-row" slot-scope="props">
                      <td v-if="props.column.field == 'manage'" style="border:0"  >
                          <button class="btn btn-danger" style="margin:5px" data-toggle="modal" data-target="#EditBomModal"  @click="deletea(props.row)"><i class="fas fa-times"></i></button>
                          <button class="btn btn-success"><i class="fas fa-check"></i></button>
                      </td>
                    </template>
                  </vue-good-table>
                    </template>
                </div>
              </div>
              <!-- end row -->
            </div>
            <!-- end card-body -->
          </div>
          <!-- end card-->
        </div>
        <!-- end col-->
      </div>
    </div>
<div class="modal fade show" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true"  >
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        ...
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary">Save changes</button>
      </div>
    </div>
  </div>
</div>


<!-- Modal -->
<div class="modal fade" id="EditBomModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Silme İşlemi</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        Stoğu silmek istediğinize emin misiniz?
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">İptal</button>
        <button type="button" class="btn btn-primary">Kaydet</button>
      </div>
    </div>
  </div>
</div>


  </main>



</template>

<script>
import  state from "../../Helper/custom_state"
export default {
  data() {
    return {
      project: "P5855925",
      rows: [],
      iftable:false,
      dataspinner:"",
      columns: [
        {
          field: "Id",
          label: "ID",         
        },
        {
          field: "Stokcode",
          label: "Stok Kodu",
           filterOptions: {
            enabled: true, // enable filter for this column
            trigger: "enter" //only trigger on enter not on keyup
          }
        },
        {
          field: "Tanim",
          label: "Tanım",
           filterOptions: {
            enabled: true, // enable filter for this column
            trigger: "enter" //only trigger on enter not on keyup
          }
        },
        {
          field: "UstStok",
          label: "Üst Stok",
           filterOptions: {
            enabled: true, // enable filter for this column
            trigger: "enter" //only trigger on enter not on keyup
          }
        },
        {
          field: "UstStokTanim",
          label: "Üst Stok Tanımı",
           filterOptions: {
            enabled: true, // enable filter for this column
            trigger: "enter" //only trigger on enter not on keyup
          }
        },
        {
          field: "manage",
          label: "Manage"
        }
      ]
    };
  },

  methods: {
    getbom: function() {
      let myself = this;
       this.dataspinner=document.getElementById("spinner");
      this.dataspinner.removeAttribute('hidden');
      fetch(`${state.ip}/api/users/bomgetir`, {
        method: "post",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          project: myself.project
        })
      })
        .then(response => {
          return response.json();
        })
        .then(response => {
          myself.rows = response;
          if(response.length>0)
          myself.iftable=true;
          myself.dataspinner.setAttribute('hidden', ''); 
          // eslint-disable-next-line no-console
        })
        .catch(function(error) {
          // eslint-disable-next-line no-console
          console.log(error.response);
        });
    },
    deletea:function(row){
       // eslint-disable-next-line no-console
         console.log(row.Id);

    }
  },


};
</script>

